<template>
  <q-page class="q-pa-md">
      <q-card flat>
        <q-card-section>
          Wähle aus, welches Instrument du gerne über die App steuern möchtest.
        </q-card-section>

        <q-card-section class="flex flex-center">
          <div class="column q-pa-md full-width">
              <q-btn padding='sm lg' rounded
                      no-caps color="primary" size='lg' 
                      class='full-width q-mb-md' 
                      :label='instrument.name'
                      v-for="instrument in instruments" :key="instrument.id"
                      @click="selectInstrument(instrument.name, instrument.id, instrument.type)">
              </q-btn>
          </div>
        </q-card-section>
      </q-card>
    </q-page>
</template>

<script>
import { useInstrumentStore } from 'stores/instruments-store';
import { getInstruments } from 'src/services/apiServices';
import { useRouter } from 'vue-router';

export default {
  setup() {
    const store = useInstrumentStore();
    const router = useRouter();
    const setInstrument = (id, name, type) => {
      store.setSelectedInstrument(id, name, type);
    };

    const goToInstrumentPage = () => {
      router.push('/instrument');
    };

    return { setInstrument, goToInstrumentPage };
  },


  data() {
    return {
      instruments: [],
    };
  },
  mounted() {
    this.fetchInstruments();
  },
  methods: {
    fetchInstruments() {
      getInstruments()
        .then(response => {
          this.instruments = response.data;
        })
        .catch(error => {
          console.error('Error fetching instruments:', error);
        });
    },
    selectInstrument(name, id, type) {
      const store = useInstrumentStore();

      console.log(`Instrument selected! '${name}' '${id}' '${type}'`);
      this.setInstrument(id, name, type);
      this.goToInstrumentPage();
    }
  }
}
</script>

<style scoped>
.fixed-width-button {
  width: 220px;
}
</style>